.role__command {
  cursor: pointer;
  font-weight: 500;
  color: white;
  padding: 4px 0;
  width: 60px;
  border-radius: 4px;
}

.role__command.none {
  background-color: #6c757d;
}

.role__command.view {
  background-color: #52c41a;
}

.role__command.edit {
  background-color: #ff4d4f;
}