}

.bottom {
  bottom: 0;
}

.button-nondecoration {
  padding: 0;
  border: none;
  background: none;
}

.w80 {
  width: 80%;
}

.coursecomp__wrapper_outdec {
  display: flex;
  justify-content: center;
}

.btn-success {
  border: none;
  background-color: #52c41a;
}

.btn-danger {
  border: none;
  background-color: #ff4d4f;
}

.btn-blank {
  border: none;
  background-color: white;
}

.btn-warning {
  border: none;
  background-color: #faad14;
}

